<template>
  <div class="countries-cities">
    <div class="select-box">
      <select class="countries" v-model="selectedCountry">
        <option
          :value="country.name"
          v-for="country of countryList"
          :key="country.id"
          >{{ country.name }}</option
        >
      </select>
      <svg viewBox="0 0 320 512">
        <path
          fill="currentColor"
          d="M41 288h238c21.4 0 32.1 25.9 17 41L177 448c-9.4 9.4-24.6 9.4-33.9 0L24 329c-15.1-15.1-4.4-41 17-41zm255-105L177 64c-9.4-9.4-24.6-9.4-33.9 0L24 183c-15.1 15.1-4.4 41 17 41h238c21.4 0 32.1-25.9 17-41z"
        ></path>
      </svg>
    </div>

    <div class="select-box">
      <select class="cities" v-model="selectedCity">
        <option disabled v-if="!selectedCountry.length > 0">
          Please select a country first!
        </option>
        <option
          disabled
          v-if="!cityList.length > 0 && selectedCountry.length > 0"
          >Sorry, there is a list of cities in {{ selectedCountry }}!</option
        >
        <option :value="city.name" v-for="city in cityList" :key="city.id">
          {{ city.name }}
        </option>
      </select>
      <svg viewBox="0 0 320 512">
        <path
          fill="currentColor"
          d="M41 288h238c21.4 0 32.1 25.9 17 41L177 448c-9.4 9.4-24.6 9.4-33.9 0L24 329c-15.1-15.1-4.4-41 17-41zm255-105L177 64c-9.4-9.4-24.6-9.4-33.9 0L24 183c-15.1 15.1-4.4 41 17 41h238c21.4 0 32.1-25.9 17-41z"
        ></path>
      </svg>
    </div>
  </div>
</template>

<script>
export default {
  name: "vueCountriesCities",
  data() {
    return {
      countryList: [],
      cityList: [],
      selectedCountry: "",
      selectedCity: ""
    };
  },
  created() {
    this.countryList = require("./data/countries.json");
  },
  watch: {
    selectedCountry() {
      this.$emit("country", this.selectedCountry);
      this.cityList = [];
      for (const country of this.countryList) {
        if (this.selectedCountry == country.name) {
          country.states.forEach(city => {
            this.cityList.push(city);
          });
        }
      }
    },
    selectedCity() {
      this.$emit("city", this.selectedCity);
    },
  },
};
</script>

<style lang="scss" scoped>
.countries-cities {
  box-sizing: border-box;
  padding: 50px;
  display: flex;
  justify-content: space-around;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  .select-box {
    display: flex;
    height: 40px;
    justify-content: space-between;
    border: 1px solid #e9e9e9;
    background: #fff;
    position: relative;
    min-width: 300px;
    select {
      outline: none;
      padding-left: 7px;
      padding-right: 65px;
      appearance: none;
      width: 100%;
      height: 100%;
      background: none;
      border: none;
    }
    svg {
      background: darken(#fff, 3%);
      pointer-events: none;
      width: 50px;
      height: 100%;
      position: absolute;
      right: 0;
      top: 0;
      box-sizing: border-box;
      padding: 5px;
      path {
        fill: rgba(black, 0.7);
      }
    }
  }
}
</style>
